.search-form {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    border: 1px solid #eee;
    border-radius: 4px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 2%;
    padding: 1rem;
}

.search-form__icon {
    color: #999;
    -ms-flex-negative: 0;
        flex-shrink: 0;
}

.search-form__input {
    -webkit-box-flex: 1;
        -ms-flex: 1 1;
            flex: 1 1;
    padding: 0 1rem;
}
